<template>
  <div class="xzxc-menu" style="overflow:hidden;">
  	<el-row :gutter="24">
      <el-col :lg="8">
        <el-form-item label="缩略图：">
          <el-tag type="danger" v-if="formtext.id == 0">文本类管理还未发布，音频不允许发布！</el-tag>
          <el-tag type="success" v-else>音频允许发布</el-tag>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row :gutter="24">
      <el-col :lg="16">
        <el-form-item label="音频上传：">
          <el-upload
            class="upload-demo"
            ref="radio"
            drag
            :action="radio.radioUploadUrl"
            :auto-upload="false"
            :with-credentials="true"
            :data="radio.uploadData"
            :on-change="radioChange"
            :file-list="radio.data"
            :on-remove="radioChangeRemove"
            :disabled="formtext.id == 0 ? true : false"
            >
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            <div class="el-upload__tip" slot="tip">只能上传audio/mp3文件，且不超过5MB</div>
          </el-upload>
        </el-form-item>
      </el-col>
    </el-row>

    <el-row :gutter="24">
      <el-col :lg="8">
        <el-form-item label="">

        	<el-button icon="el-icon-service" v-if="formtext.id == 0 && radio.success == 0" disabled>确认上传音频</el-button>
	          <el-button type="success" icon="el-icon-service" v-else-if="formtext.id != 0 && radio.success == 0" @click="saveRadio">确认上传音频</el-button>
	          <el-button type="success" icon="el-icon-service" v-else-if="formtext.id != 0 && radio.success == 1" disabled>音频已保存成功</el-button>
	          <i class="el-icon-success" v-if="formtext.id != 0 && radio.success == 1" style="color:#67c23a;font-size:30px;    vertical-align: middle;"></i>



          
        </el-form-item>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import CommonImg from '../commonimg'
export default {
  components: {
      CommonImg
    },
  props: ['formtext'],
  data () {
    return {
     radio: {
     	radioUploadUrl: '',
     	uploadData:{
          urlJurisdiction: window.location.hash.replace('#','')
        },
        data: [],
        emotion_id: 0,
        success: 0,
        userid: 0,
        routeQuery: {},
        adminlevel: 0,
        userInfo: null
     }
    }
  },
  created () {
    this.radio.emotion_id = this.formtext.id
    this.radio.userInfo = this.$store.state.userInfo
    this.radio.adminlevel = this.radio.userInfo ? this.radio.userInfo.level : 0
    this.radio.routeQuery = this.$route.query.emotion_id ? this.$route.query : null
    this.radio.radioUploadUrl = this.api.radioUpload
  },
  computed: {
   
  },
  mounted () {
  	
  },
  methods: {
  	saveRadio () {
  	  var _this = this
  	  if (this.$refs.radio.fileList.length <= 0) {
        _this.alerter('请选择文件再点击上传！')
        return false
      }
      const formData_ = new FormData();
      this.$refs.radio.fileList.map(function(item,index){
        formData_.append('file' + index, item.file)
      })
      var url
      if (_this.routeQuery && _this.routeQuery.emotion_id) {
        _this.userid = _this.routeQuery.userid || _this.userid
        url = _this.api.radioUpload + '?urlJurisdiction=' + window.location.hash.replace('#','').split('?')[0] + '&userid=' + this.radio.userid + '&adminlevel=' + this.radio.adminlevel + '&emotion_id=' + _this.routeQuery.emotion_id

      } else {
        url = _this.api.radioUpload + '?urlJurisdiction=' + window.location.hash.replace('#','').split('?')[0] + '&userid=' + this.radio.userid + '&adminlevel=' + this.radio.adminlevel + '&emotion_id=' + _this.formtext.id
      }
      _this.$post({
          url: url,
          data: {
            formData_,
            headers: {
              'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
              'content-type': 'application/json; charset=utf-8'
            }
          },
          config: {
            
          },
          success (res){
            if (res.data.code == 200) {
              _this.radio.success = 1
              _this.alertsu(res.data.msg)
            } else {
              _this.alerter(res.data.msg)
            }
          },
          errror () {

          }
        })
  	},
    radioChange (files) {
    	console.log(files)
    	var _this = this
    	if (_this.$refs.radio.fileList.length >= 1) {
    		console.log(_this.$refs.radio.fileList)
    		_this.alerter('音频每个课时只允许上传一节！')
			return false
    	}
		if (files.raw.type !== 'audio/mp3'){
			_this.alerter('请选择audio/mp3格式音频上传！')
			return false
		}
		if (files.name) {
			if (files.size / 1024 / 1024 > 5) {
			  _this.alerter('上传文件不能大于5MB！')
			  return false
			}
		}
		var reader=new FileReader();
		reader.readAsDataURL(files.raw);
		reader.onload=function(){
			_this.radio.data.push({name: files.name, file: files.raw,url:reader.result}) 
		}
    },
  	radioChangeRemove (item) {
  	  var i
      var file = this.$refs.radio.fileList.filter(function(items, index){
        if (items.uid == item.uid) {
          i = index
          return true
        }
      })
      this.$refs.radio.fileList.splice(i,1)
      this.radio.data = this.$refs.radio.fileList
      console.log(this.$refs.radio.fileList)
  	}
  },
  updated () {
  },
  destroyed () {
  },
  deactivated () {
  },
  activated () {
  }
}
</script>
<style>

</style>



