<template>
  <div class="xzxc-menu" style="overflow:hidden;">
    <el-tabs type="border-card">
      <el-tab-pane>
        <span slot="label"><i class="el-icon-date"></i> 图文编辑</span>


      <el-row :gutter="24">
          <el-col :lg="8">
            <el-form-item label="缩略图：">
              <el-tag type="danger" v-if="formtext.id == 0">文本类管理还未发布，图文不允许编辑！</el-tag>
              <el-tag type="success" v-else>图文允许编辑</el-tag>
            </el-form-item>
          </el-col>
        </el-row>

      <el-card class="box-card" style="margin-bottom:20px;" v-for="(item, index) in img.pictureText" :key="index">
          <div slot="header" class="clearfix">
            <span>输入标题和内容</span>
            <el-button style="float: right;" size="mini" round type="danger" @click="closeCard(index)">关闭</el-button>
          </div>

          <el-row :gutter="24">
            <el-col :lg="24">
                <el-form-item>
                  <el-button size="mini" icon="el-icon-search" round @click="selectImg(item)">选取图片</el-button>
                  <img v-if="item.img.length > 0" v-for="imgi in item.img" :src="imgpath + '/' +imgi" style="width:30px;height:30px;border-radius:15px;margin-left:10px;vertical-align:middle;">
                </el-form-item>
                <el-form-item label="标题">
                  <el-input v-model="item.title" placeholder="输入标题"></el-input>
                </el-form-item>
                <el-form-item label="内容">
                  <el-input
                    type="textarea"
                    :rows="3"
                    placeholder="请输入内容"
                    v-model="item.desc">
                  </el-input>
                </el-form-item>
            </el-col>
          </el-row>
      </el-card>

      <el-row :gutter="24">
        <el-col :lg="24">
          <el-form-item label="文本添加：">
            <el-button icon="el-icon-plus" circle style="font-size:20px;" disabled v-if="formtext.id == 0"></el-button>
            <el-button type="primary" icon="el-icon-plus" circle style="font-size:20px;" v-else @click="addtext"></el-button>
          </el-form-item>
        </el-col>
      </el-row>


      <el-row :gutter="24">
        <el-col :lg="24">
          <el-form-item label="">
            <el-button icon="el-icon-plus" style="font-size:20px;" disabled v-if="formtext.id == 0 && img.pictureText.length <= 0">提交保存</el-button>
            <el-button type="primary" icon="el-icon-plus" v-else-if="formtext.id > 0 && img.pictureText.length > 0" @click="submitTextPicture">提交保存</el-button>
          </el-form-item>
        </el-col>
      </el-row>


      </el-tab-pane>

    </el-tabs>
    
  	<!-- <quill-editor ref="quillEditor" style="height:500px" v-model="content" @change="onEditorChange($event)"></quill-editor> -->
    <el-dialog
      title="选取图片"
      :visible.sync="selectShow"
       :width="this.$store.state.modalWidth">
      <img v-if="img.data.length > 0" style="height:80px;margin-right:10px;margin-bottom:10px;border-radius:3px;cursor: pointer;" v-for="(imgitem, index) in img.data" :src="imgitem.url" @click="getImg(imgitem, index)">
      <el-alert
        v-else
        title="请上传图片！"
        type="error">
      </el-alert>
      <span slot="footer" class="dialog-footer">
        <el-button @click="selectShow = false">取 消</el-button>
        <el-button type="primary" @click="addSetImg">确 定</el-button>
      </span>
    </el-dialog>

    <common-img v-if="commonimgshow" :closeCommonImg="closeCommonImgFn" :getImg="getImgFn" :setImg="item"></common-img>


  </div>
</template>

<script>
import CommonImg from '../commonimg'
export default {
  components: {
      CommonImg
    },
  props: ['formtext'],
  data () {
    return {
     commonimgshow: false, 
     imgpath: this.$store.state.staticPath,
     selectShow: false,
     img: {
        slideUploadUrl: '',
        data: [],
        uploadData:{
          urlJurisdiction: window.location.hash.replace('#','')
        },
        pictureText: [],
        emotion_id: 0
     },
     item: null
    }
  },
  created () {
  	console.log(this.$refs.quillEditor, '**')
    this.img.emotion_id = this.formtext.id
  },
  computed: {
   
  },
  mounted () {
  	
  },
  methods: {
    closeCard (index) {
      this.img.pictureText.splice(index,1)
    },
    submitTextPicture () {
      var _this = this
      if (_this.img.pictureText.length <= 0) {
        return _this.alerter('请添加图文！')
      } else {
        var item = _this.img.pictureText.filter(function (item,index) {
          if (item.title == '') {
            return false
          }
          if (item.desc == '') {
            return false
          }
          if (item.img.length <= 0) {
            return false
          } else {
            return true
          }
        })
        if (item.length != _this.img.pictureText.length) {
          return _this.alerter('还有图文模块没有填写完整！')
        }
      }
      _this.img.emotion_id = this.formtext.id
      _this.$axios({
         type: '$axios',
         obj: {
          url: _this.api.uploadImgText,
          method: 'post',
          success (res) {
            if (res.data.code == 200) {
              _this.alertsu(res.data.msg)
            } else {
              _this.alerter(res.data.msg)
            }
          },
          error () {
            _this.alertsu('情感数据保存失败！')
          },
          data: _this.img,
          header: {
            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
            'content-type': 'application/json; charset=utf-8'
          }
        }
      })
    },
    getImgFn (imgarr) {
      this.item.img = imgarr
    },
    addtext () {
      this.img.pictureText.push({
        title: '',
        desc: '',
        img: []
      })
    },
    closeCommonImgFn () {
      this.commonimgshow = false
    },
    selectImg (item) {
      this.commonimgshow = true
      this.item = item
    },
    addSetImg () {

    },
    getImg (item,index) {
      this.item.img.push({
        index:index,
        path:item.url
      })
      console.log(this.img.pictureText)
    }
  },
  updated () {
  },
  destroyed () {
  },
  deactivated () {
  },
  activated () {
  }
}
</script>
<style>

</style>



