<template>
  <div class="xzxc-menu">
    <br>
    <el-form v-if="admin && edit" :label-position="this.$store.state.panelLayout" label-width="100px">
      <el-row :gutter="24">
        <el-col :lg="8">
          <el-form-item label="选择用户：">
            <get-user-data :getUserIdFn="getSetUser"></get-user-data> 
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    
    <el-form :model="formtext" ref="formtext" :label-position="this.$store.state.panelLayout" :rules="formtextRules" label-width="100px">
      <el-tabs type="border-card">
        <el-tab-pane label="文本类管理">
          <span slot="label"><i class="el-icon-date"></i> 文本类管理</span>
          <el-row :gutter="24">
            <el-col :lg="8">
              <el-form-item label="课程标题：" prop="title">
                <el-input :placeholder="formPlaceholder.title" v-model="formtext.title"></el-input>
              </el-form-item>
            </el-col>
          </el-row>




          <el-row :gutter="24">
            <el-col :lg="8">
              <el-form-item label="选择分类：" prop="class">
                <el-select v-model="formtext.class" :placeholder="formPlaceholder.class" @change="classChange" style="width:100%;">
                  <el-option v-for="item in formtext.themeData" :key="item.id" :label="item.theme_name" :value="item.id">{{item.theme_name}}</el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>


          


          <el-row :gutter="24">
            <el-col :lg="16">
              <el-form-item label="简单描述：" prop="desc">
                <el-input :rows="3" type="textarea" placeholder="输入描述" v-model="formtext.desc"></el-input>
              </el-form-item>
            </el-col>

          </el-row>

          <el-row :gutter="24">
            <el-col :lg="8">
              <el-form-item label="作者：" prop="name">
                <el-input placeholder="输入发布人昵称" v-model="formtext.name"></el-input>
              </el-form-item>
            </el-col>

            <el-col :lg="8">
              <el-form-item label="现价：" prop="price">
                <el-input placeholder="输入现在价格" type="number" v-model="formtext.price"></el-input>
              </el-form-item>
            </el-col>

          </el-row>


          <el-row :gutter="24">
            <el-col :lg="8">
              <el-form-item label="订购量：" prop="orderNumber">
                <el-input placeholder="输入订购数量" type="number" v-model="formtext.orderNumber"></el-input>
              </el-form-item>
            </el-col>
            <el-col :lg="8">
              <el-form-item label="原价：" prop="originalPrice">
                <el-input placeholder="输入原先价格" type="number" v-model="formtext.originalPrice"></el-input>
              </el-form-item>
            </el-col>
          </el-row>




          <el-row :gutter="24">
            <el-col :lg="8">
              <el-form-item label="阅读量：" prop="readNumber">
                <el-input placeholder="阅读量" type="number" v-model="formtext.readNumber"></el-input>
              </el-form-item>
            </el-col>
            
          </el-row>




          <el-row :gutter="24" v-if="admin">
            <el-col :lg="16">
              <el-form-item label="属性：">
                <el-checkbox-group v-model="formtext.checkListCheckbox">
                  <el-checkbox v-for="item in formtext.checkListCheckboxArr" :label="item" :key="item">{{item}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
            </el-col>
          </el-row>


          <el-row :gutter="24">
              <el-col :lg="8">
                <el-form-item label="">
                  <el-button type="primary" @click="textFormValidata">提交保存</el-button>
                </el-form-item>
              </el-col>
              
            </el-row>
      </el-tab-pane>
    </el-tabs>      
  </el-form>  

          <br>

      <el-form :label-position="this.$store.state.panelLayout" :model="formmedia" label-width="160px">
          <el-row :gutter="24">
            <el-col :lg="24">
                <el-tabs type="border-card">
                  <el-tab-pane>
                    <span slot="label"><i class="el-icon-picture"></i> 媒体上传类编辑</span>

                    <el-row :gutter="24">
                      <el-col :lg="8">
                        <el-form-item label="幻灯片发布状态：">
                          <el-tag type="danger" v-if="formtext.id == 0">文本类管理还未发布，幻灯片不允许发布！</el-tag>
                          <el-tag type="success" v-else>幻灯片允许发布</el-tag>
                        </el-form-item>
                      </el-col>
                    </el-row>

                    <el-row :gutter="24">
                      <el-col :lg="16">
                        <el-form-item label="幻灯片：">
                          <el-upload
                            ref="slide"
                            class="upload-demo"
                            drag
                            :action="slide.slideUploadUrl"
                            :file-list="slide.data"
                            :on-change="slideChange"
                            :auto-upload="false"
                            :with-credentials="true"
                            :data="slide.uploadData"
                            :on-remove="slideRemove"
                            :before-upload="beforeEditSlide"
                            list-type="picture"
                            :disabled="formtext.id == 0 ? true : false"
                            multiple>
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                          </el-upload>
                        </el-form-item>
                      </el-col>
                    </el-row>

                    <el-row :gutter="24">
                      <el-col :lg="8">
                        <el-form-item label="">
                          <el-button icon="el-icon-picture-outline" v-if="formtext.id == 0 && slide.success == 0" disabled>保存幻灯片</el-button>
                          <el-button type="primary" icon="el-icon-picture-outline" v-else-if="formtext.id != 0 && slide.success == 0" @click="saveSlide">保存幻灯片</el-button>
                          <el-button type="success" icon="el-icon-check" v-else-if="formtext.id != 0 && slide.success == 1" disabled>幻灯片已保存成功！</el-button>
                          <i class="el-icon-success" v-if="formtext.id != 0 && slide.success == 1" style="color:#67c23a;font-size:30px;vertical-align: middle;"></i>
                        </el-form-item>
                      </el-col>
                    </el-row>

                    <hr>


                    <el-row :gutter="24">
                      <el-col :lg="8">
                        <el-form-item label="缩略图：">
                          <el-tag type="danger" v-if="formtext.id == 0">文本类管理还未发布，缩略图不允许发布！</el-tag>
                          <el-tag type="success" v-else>缩略图允许发布</el-tag>
                        </el-form-item>
                      </el-col>
                    </el-row>

                    <el-row :gutter="24">
                      <el-col :lg="8">
                        <el-form-item label="缩略图：">
                          <el-upload
                            class="upload-demo"
                            ref="abbreviations"
                            drag
                            :action="abbreviationsImg.emotionUploadMinImgUrl"
                            :auto-upload="false"
                            :with-credentials="true"
                            :data="abbreviationsImg.uploadData"
                            list-type="picture"
                            :on-change="abbreviationsImgChange"
                            :file-list="abbreviationsImg.data"
                            :on-remove="abbreviationsImgRemove"
                            :disabled="formtext.id == 0 ? true : false">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过100kb</div>
                          </el-upload>
                        </el-form-item>
                      </el-col>
                    </el-row>
                    <el-row :gutter="24">
                      <el-col :lg="8">
                        <el-form-item label="">
                          <el-button icon="el-icon-picture" v-if="formtext.id == 0 && abbreviationsImg.success == 0" disabled>保存缩略图</el-button>
                          <el-button type="primary" icon="el-icon-picture" v-else-if="formtext.id != 0 && abbreviationsImg.success == 0" @click="abbreviationsImgFn">保存缩略图</el-button>
                          <el-button type="success" icon="el-icon-check" v-else-if="formtext.id != 0 && abbreviationsImg.success == 1" disabled>缩略图已保存成功</el-button>
                          <i class="el-icon-success" v-if="formtext.id != 0 && abbreviationsImg.success == 1" style="color:#67c23a;font-size:30px;    vertical-align: middle;"></i>
                        </el-form-item>
                      </el-col>
                    </el-row>

                    <hr>

                    <text-img :formtext="formtext"></text-img>
                    

                    <hr>

                    <radio-upload ref="radioid" :formtext="formtext"></radio-upload>

                    <hr>

                    <video-upload ref="videoid" v-if="admin" :formtext="formtext"></video-upload>

                  </el-tab-pane>
                </el-tabs>
            </el-col>
          </el-row>

        </el-form>


        




  </div>
</template>

<script>
import GetUserData from '../getUserData'
import RadioUpload from './radioUpload'
import VideoUpload from './videoUpload'
import TextImg from './textimg'
export default {
  components: {
      GetUserData,
      TextImg,
      RadioUpload,
      VideoUpload
    },
  data () {
    return {
      fileList: [],
      checked: '1',
      num1:1,
      classData: null,
      formtextRules: {
        title: {required: true,message: '请输入课程标题'},
        class: {required: true,message: '请选择主题分类'},
        desc: {required: true,message: '请输入描述'},
        name: {required: true,message: '请输入名称'},
        price: [
          {required: true,message: '请输入现价'}
        ],
        orderNumber: {required: true,message: '请输入订购数量'},
        originalPrice: [
          {required: true,message: '请输入原价'}
        ],
        readNumber: {required: true,message: '请输入阅读数量'},
      },
      
      formmedia: {
        imgtextArr: [
        ],
        title:'',
        name: '',
        theme: '',
        class: '',
        desc: '',
        minimg: '',
        price: 0,
        OriginalPrice: 0,
        uploadFileUrl: '',
        ReadNumber: 0,
        files: null,
        checkListCheckbox: ['推荐'],
        checkListCheckboxArr: [
          '推荐',
          '置顶',
          '经典'
        ],
        themeData: []
      },
      formtext: {
        title:'',
        name: '',
        class: '',
        desc: '',
        readNumber: '',
        originalPrice: '',
        orderNumber: '',
        price: '',
        checkListCheckbox: [],
        checkListCheckboxArr: [
          '推荐',
          '置顶',
          '经典'
        ],
        themeData: [],
        adminlevel: 0,
        userid: 0,
        user: null,
        id: 0,
        edit: false
      },
      slide: {
        data: [],
        slideUploadUrl: '',
        files: [],
        uploadData:{
          urlJurisdiction: window.location.hash.replace('#','')
        },
        success: 0
      },
      // 缩略图
      abbreviationsImg: {
        emotionUploadMinImgUrl: '',
        data: [],
        files: [],
        uploadData:{
          urlJurisdiction: window.location.hash.replace('#','')
        },
        success: 0
      },

      minimgBase64: null,
      formPlaceholder: {
        title:'请输入标题',
        name: '请输入作者',
        theme: '请输入主题',
        class: '请选择分类',
        desc: '请输入描述',
        minimg: '选择缩略图'
      },
      admin: false,
      edit: true,
      userid: 0,
      adminlevel: 0,
      routeQuery: null
    }
  },
  beforeRouteUpdate (to, from, next) {
    next()
    location.reload()
  },
  created () {
    this.formtext.uploadFileUrl = this.api.emotioinminimg
    this.slide.slideUploadUrl = this.api.slideUploadUrl
    var _this = this
      this.userInfo = this.$store.state.userInfo
      this.adminlevel = this.formtext.adminlevel =  this.userInfo ? this.userInfo.level : 0
    if (this.userInfo) {
      if (this.userInfo.level === 1) {
        this.admin = true
      }
    }
    _this.routeQuery = this.$route.query.emotion_id ? this.$route.query : null
    if (_this.routeQuery && _this.routeQuery.emotion_id) {
      _this.formtext.id = _this.routeQuery.emotion_id
      _this.edit = false
      _this.formtext.edit = true
      this.userid = this.formtext.userid = _this.routeQuery.user_id
      _this.getEmotionData()
    }
    _this.gettheme()
  },
  methods: {
    classChange (val) {
      console.log(this.formtext.class)
    },
    beforeEditSlide (file) {
      
    },
    getEmotionData () {
      var _this = this
        _this.$get({
          type: '$get',
          obj: {
            url: _this.api.getEmotionData,
            success: function (res) {
              if (res.data.code == 200) {
                // _this.formtext.themeData = res.data.data
                var data = res.data.data[0]
                _this.formtext.title = data.title
                _this.formtext.name = data.author_name
                _this.formtext.class = data.theme_id
                _this.formtext.desc = data.emotion_describe
                _this.formtext.readNumber = data.readNumber
                _this.formtext.originalPrice = data.originalPrice
                _this.formtext.orderNumber = data.orderNumber
                _this.formtext.price = data.price
                _this.formtext.checkListCheckbox = data.attribute.split(',')
                _this.alertsu('获取菜单数据成功！','获取菜单数据',2000)
              }
              else {
                _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
              }

            },
            error: function () {
              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
            }
          },
          param: {
            emotion_id: _this.formtext.id,
            adminlevel: this.adminlevel
          }
      })
    },
    saveSlide () {
      var _this = this
      if (this.$refs.slide.fileList.length <= 0) {
        _this.alerter('请选择文件再点击上传！')
        return false
      }
      const formData_ = new FormData();
      this.$refs.slide.fileList.map(function(item,index){
        formData_.append('file' + index, item.file)
      })
      var url
      if (_this.routeQuery && _this.routeQuery.emotion_id) {
        _this.userid = _this.routeQuery.userid || _this.userid
        url = _this.api.slideUploadUrl + '?urlJurisdiction=' + window.location.hash.replace('#','').split('?')[0] + '&userid=' + this.userid + '&adminlevel=' + this.adminlevel + '&emotion_id=' + _this.routeQuery.emotion_id

      } else {
        if (_this.formtext.id == 0) {
          _this.alerter('请先发布完文本类管理再发布幻灯片！')
          return false
        }
        url = _this.api.slideUploadUrl + '?urlJurisdiction=' + window.location.hash.replace('#','').split('?')[0] + '&userid=' + this.userid + '&adminlevel=' + this.adminlevel + '&emotion_id=' + _this.formtext.id
      }
      _this.$post({
          url: url,
          data: {
            formData_,
            headers: {
              'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
              'content-type': 'application/json; charset=utf-8'
            }
          },
          config: {
            
          },
          success (res){
            if (res.data.code == 200) {
              _this.slide.success = 1
              _this.alertsu(res.data.msg)
            } else {
              _this.alerter(res.data.msg)
            }
          },
          errror () {

          }
        })
    },
    slideChange (files) {
      var _this = this
      if (files.raw.type !== 'image/png' && files.raw.type !== 'image/jpeg' && files.raw.type !== 'image/gif'){
        this.$refs.slide.clearFiles()
        _this.alerter('请选择png/jpg/gif格式图片上传！')
        return false
      }
      if (files.name) {
        if (files.size / 1024 / 1024 > 0.5) {
          this.$refs.slide.clearFiles()
          _this.alerter('上传文件不能大于500K！')
          return false
        }
      }
      var reader=new FileReader();
      reader.readAsDataURL(files.raw);
      reader.onload=function(){
        _this.slide.data.push({name: files.name, file: files.raw,url:reader.result}) 
      }
    },
    slideRemove (item) {
      var i
      var file = this.$refs.slide.fileList.filter(function(items, index){
        if (items.uid == item.uid) {
          i = index
          return true
        }
      })
      this.$refs.slide.fileList.splice(i,1)
      this.slide.data = this.$refs.slide.fileList
    },
    // 缩略图点击保存
    abbreviationsImgFn () {
      var _this = this
      console.log(this.$refs.abbreviations.fileList)
      if (this.$refs.abbreviations.fileList.length <= 0) {
        _this.alerter('请选择文件再点击上传！')
        return false
      }
      const formData_ = new FormData();
      formData_.append('file', this.$refs.abbreviations.fileList[0].file)
      var url
      if (_this.routeQuery && _this.routeQuery.emotion_id) {
        _this.userid = _this.routeQuery.userid || _this.userid
        url = _this.api.emotionUploadMinImg + '?urlJurisdiction=' + window.location.hash.replace('#','').split('?')[0] + '&userid=' + this.userid + '&adminlevel=' + this.adminlevel + '&emotion_id=' + _this.routeQuery.emotion_id

      } else {
        if (_this.formtext.id == 0) {
          _this.alerter('请先发布完文本类管理再发布幻灯片！')
          return false
        }
        url = _this.api.emotionUploadMinImg + '?urlJurisdiction=' + window.location.hash.replace('#','').split('?')[0] + '&userid=' + this.userid + '&adminlevel=' + this.adminlevel + '&emotion_id=' + _this.formtext.id
      }
      _this.$post({
          url: url,
          data: {
            formData_,
            headers: {
              'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
              'content-type': 'application/json; charset=utf-8'
            }
          },
          config: {
            
          },
          success (res){
            if (res.data.code == 200) {
              _this.abbreviationsImg.success = 1
              _this.alertsu(res.data.msg)
            } else {
              _this.alerter(res.data.msg)
            }
          },
          errror () {

          }
        })
      console.log(url)
    },
    // 缩略图删除
    abbreviationsImgRemove (item) {
      var i
      var file = this.$refs.abbreviations.fileList.filter(function(items, index){
        if (items.uid == item.uid) {
          i = index
          return true
        }
      })
      this.$refs.abbreviations.fileList.splice(i,1)
      this.abbreviationsImg.data = this.$refs.abbreviations.fileList
    },
    // 缩略图选择文件
    abbreviationsImgChange (files) {
      var _this = this
      if (files.raw.type !== 'image/png' && files.raw.type !== 'image/jpeg' && files.raw.type !== 'image/gif'){
        this.$refs.abbreviations.clearFiles()
        _this.alerter('请选择png/jpg/gif格式图片上传！')
        return false
      }
      if (files.name) {
        if (files.size / 1024 / 1024 > 0.1) {
          this.$refs.abbreviations.clearFiles()
          _this.alerter('上传文件不能大于500K！')
          return false
        }
      }
      var reader=new FileReader();
      reader.readAsDataURL(files.raw);
      reader.onload=function(){
        _this.abbreviationsImg.data.push({name: files.name, file: files.raw,url:reader.result}) 
      }
    },
    textFormSave () {
      var _this = this
      _this.$axios({
         type: '$axios',
         obj: {
          url: _this.api.addemotiontext,
          method: 'post',
          success (res) {
            if (res.data.code == 200) {
              console.log(res, '*******', _this.routeQuery)
              if (!_this.routeQuery) {
                _this.formtext.id = res.data.data.id
                _this.formtext.edit = true
              }
              _this.alertsu(res.data.msg)
            } else {
              _this.alerter(res.data.msg)
            }
          },
          error () {
            _this.alertsu('情感数据保存失败！')
          },
          data: _this.formtext,
          header: {
            'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
            'content-type': 'application/json; charset=utf-8'
          }
        }
      })
    },
    textFormValidata () {
      var _this = this
      if (_this.formtext.themeData.length <= 0) {
        _this.$message.error({
          message: '当前用户没有创建主题，不能发布课程！',
          duration: 6000
        });
        _this.alerter('当前用户没有创建主题，不能发布情感数据！','',6000)
        return false
      }
      _this.$refs.formtext.validate((valid) => {
        if (valid) {
          _this.textFormSave()
        } else {
          return false
        }
      })
    },
    getSetUser (user) {
        this.$refs.formtext.resetFields();
        this.formtext.id = 0
        this.slide.success = 0
        this.abbreviationsImg.success = 0
        this.user = this.formtext.user = [user]
        this.userid = this.formtext.userid = user.id
        this.$refs.radioid.radio.success = 0
        this.$refs.videoid.radio.success = 0
        this.gettheme()
    },
    
    // 获取主题
    gettheme () {
      var _this = this
        _this.$get({
        type: '$get',
        obj: {
          url: _this.api.gettheme,
          success: function (res) {
            if (res.data.code == 200) {
              _this.formtext.themeData = res.data.data
              if (_this.formtext.themeData.length <= 0) {
                _this.$message.error({
                  message: '当前用户没有创建主题，不能发布课程！',
                  duration: 6000
                });
              }
              _this.alertsu('获取菜单数据成功！','获取菜单数据',2000)
            }
            else {
              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
            }

          },
          error: function () {
            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
          }
        },
        param: {
          userid: this.userid,
          adminlevel: this.adminlevel
        }
      })
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleRemove2(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview2(file) {
      console.log(file);
    },
    submitUpload() {},
    // 缩略图选择时触发
    minimgChange (files,obj) {
      var _this = this
      _this.form.minimg = files.name
      _this.form.files = files.raw

      

      if (files.raw.type !== 'image/png' && files.raw.type !== 'image/jpeg' && files.raw.type !== 'image/gif'){
        _this.form.minimg = ''
        _this.minimgBase64 = null
        _this.$refs.uploademotioin.abort()
        _this.$refs.uploademotioin.clearFiles()
        _this.alerter('请选择png/jpg/gif格式图片上传！')
        return false
      }
      if (files.name) {
        if (files.size / 1024 / 1024 > 0.1) {
          _this.form.minimg = ''
          _this.minimgBase64 = null
          _this.$refs.uploademotioin.abort()
          _this.$refs.uploademotioin.clearFiles()
          _this.alerter('上传文件不能大于100K！')
        } else {
          var reader=new FileReader();
          reader.readAsDataURL(_this.form.files);
          reader.onload= function(){
              _this.minimgBase64 = this.result; 
          }
        }
      }
    }
  }

}
</script>
<style lang="less">
.el-select-dropdown__list{
      overflow: scroll;
}
</style>



