<template>
  <div class="xzxc-common-img">
  	<br>
    
  	
    <el-dialog
	  title="图片选取"
	  :visible.sync="dialogVisible"
	  :width="this.$store.state.modalWidth"
	  :show-close="false"
	  :close-on-click-modal="false"
	  >


	  <el-form v-if="admin" :label-position="this.$store.state.panelLayout" label-width="100px">
	      <el-row :gutter="24">
	        <el-col :lg="8">
	          <el-form-item label="选择用户：">
	            <get-user-data :getUserIdFn="getSetUser"></get-user-data> 
	          </el-form-item>
	        </el-col>
	      </el-row>
	   </el-form>


	  <el-row :gutter="24" v-if="imgLibsTableData && imgLibsTableData.length > 0">
	  	<el-checkbox-group v-model="checkList" @change="checkListChang">
	  	<el-col :lg="3" v-for="item in imgLibsTableData" :key="item.id">
	  		<div class="common-img-box">
	  			<el-checkbox :label="item.img_path" style="position:absolute;top:0px;left:0px;width:100%;height:100%;text-align: center;
    line-height: 80px;">&nbsp;</el-checkbox>
	  			<img :src="imgpath + '/' + item.img_path">
	  		</div>
	  	</el-col>
	  	</el-checkbox-group>
	  </el-row>
	  <el-button type="warning" v-else @click="goImglibs">图库无图片，去图库上传图片</el-button>

	  <div class="paging-box">
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pager.currentPage3"
          :page-size="pager.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pager.count">
        </el-pagination>
      </div>



	  <span slot="footer" class="dialog-footer">
	    <el-button type="primary" @click="getCommonImg">确 定</el-button>
	  </span>
	</el-dialog>



    
  </div>
</template>

<script>
import GetUserData from './getUserData'
export default {
  props: ['closeCommonImg','getImg', 'setImg'],
  components: {
    GetUserData,
  },
  data () {
    return {
      checkList: [],
      dialogVisible: true,
      userid: 0,
      userInfo: null,
      adminlevel: 0,
      admin: false,
      imgLibsTableData: null,
      imgpath: this.$store.state.staticPath,
      pager: {
        currentPage3: 1,
        count: 0,
        pageSize: 30,
        size: 0,
        user_name: ''
      }
    }
  },
  created () {
    var _this = this
    _this.checkList = _this.setImg ? _this.setImg.img : []
    _this.userInfo = _this.$store.state.userInfo
    _this.adminlevel =  _this.userInfo ? _this.userInfo.level : 0
    if (_this.userInfo) {
      if (_this.userInfo.level === 1) {
        _this.admin = true
      }
    }
    _this.getImgLibsData()
  },
  methods: {
  	goImglibs () {
  		this.$router.push({
  			name: 'imglibs'
  		})
  	},
  	checkListChang(){
  		this.getImg(this.checkList)
  	},
  	getCommonImg () {
  		this.closeCommonImg()
  	},
    handleSizeChange (val) {
      this.pager.pageSize = val
      this.getImgLibsData()
    },
    handleCurrentChange (val) {
      this.pager.size = val - 1
      this.getImgLibsData()
    },
    getSetUser (user) {
        this.userid = user.id
        this.pager.size = 0
        this.pager.pageSize = 30
        this.pager.count = 0
        this.getImgLibsData()
    },
    deleteImg (item,type) {
      var _this = this
      this.$axios({
           type: '$axios',
           obj: {
            url: _this.api.deleteImgLib,
            method: 'post',
            success (res) {
              if (res.status == 200) {
                _this.alertsu('用户禁用成功！')
                _this.getImgLibsData()
              } else {
                _this.alertsu('用户禁用失败！')
              }
            },
            error () {
              _this.alertsu('用户禁用失败！')
            },
            data: {
              id: item.id,
              type: type
            },
            header: {
              'x-csrf-token': _this.getCatch(_this, 'csrfToken'),
              'content-type': 'application/json; charset=utf-8'
            }
          }
      })
    },
  	getImgLibsData () {
      var _this = this
      _this.$get({
      type: '$get',
      obj: {
        url: _this.api.getlibsimg,
        success: function (res) {
          if (res.data.code == 200) {
            console.log(res)
            _this.imgLibsTableData = res.data.data.imgdata
            _this.pager.count = res.data.data.count
          }
          else {
            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
          }

        },
        error: function () {
          _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
        }
      },
      param: {
        userid: this.userid,
        size: _this.pager.size,
        sizes: _this.pager.pageSize,
        adminlevel: this.adminlevel
      }
    })
    }
  }
}
</script>
<style>
	.common-img-box{
		height:80px;position:relative;overflow:hidden;border-radius:6px;margin-bottom:20px;
	}
	.common-img-box img{height:80px;}
	.xzxc-common-img .el-checkbox__label{display:none;}
</style>



